@media (max-width: 700px) {
    .grid-about{
        grid-template-columns: repeat( auto-fit, minmax(100px, 1fr) )!important;
    }
    .contact .nav-right-down, .body-about .nav-right-down {
        display: none;
    }
}
@media (max-width: 1100px) {
    
    .nav-right-down {
        left: 20px!important;
    }
    .about {
        width: 90%!important;
        margin: 100px auto !important;
        padding: 0!important;
    }
    .nav-right-down {
        left: 20px!important;
    }
    
    .caja:nth-child(n+2) img {
        width: var(--anchoCajaMedia);
    }
    .caja h1 {
        font-size: 2em !important;
    }
    .contact_form {
        width: 80%!important;
    }
    .animado {
        opacity: 1;
    }
}
@media (min-width: 1100px) {
    .animado {
        opacity: 0.5;
    }
}